				swiperLength: 0,
				carouselList: [],
				goodsList: [],
				teacher:{
					name: '邱钰杰',
					unit:'校团委二课中心',
					indentify:'团委书记',
					volunteer_number:'822',
					volunteer_rate:'82.2',
					pass_number:'523',
					pass_rate:'52.3',
					warning_number:'122',
					warning_rate:'12.2'
				},
				classes:{
					classnumber:'35',
					activitynumber:'122',
					judge1:'71.12',
					judge2:'32.24',
					overcome:'85.66',
					participate:'72.64'
				},
				DataCloud:{
				 "series": [
				        {
				            "name": "海空学院girls，向未来！",
				            "textSize": 20
				        },
						{
						    "name": "第二届“海空杯”海洋科技创新创业大赛",
						    "textSize": 18
						},
						{
						    "name": "探索海洋之谜”海洋科普知识竞赛",
						    "textSize": 19
						},
						{
						    "name": "“华测导航杯”第十八届测绘技能大赛",
						    "textSize": 20
						},
							]
				},
				AverageData:{
					    "categories": [
					        "2018",
					        "2019",
					        "2020",
					        "2021"
					    ],
					    "series": [
					        {
					            "name": "思想成长",
					            "data": [
					               86,
								   78,
								   43,
								   21
					            ]
					        },
					        {
					            "name": "文体发展",
					            "data": [
					                78,
					                69,
					                39,
					                21
					                
					            ]
					        },
							{
							    "name": "创新创业",
							    "data": [
							        63,
							        55,
							        35,
							        19
							        
							    ]
							},
							{
							    "name": "社会实践与志愿服务",
							    "data": [
							        58,
							        62,
							        36,
							        16
							        
							    ]
							}
					    ]
				}
			};
		},



		onLoad() {
			this.loadData();
		},
		methods: {
			/* getcarousedata(){         /*调用接口数据
				uni.request({
				url="";
				success:(res)=>{
					this.carouselList=res.data;
				}
				})
			} */

			gotoload() {
				uni.navigateTo({
					url: "/pages/download/download"
				})
			},

			gotoFAQ() {
				uni.navigateTo({
					url: "/pages/FAQ/FAQ"
				})
			},

			gotoXueshi() {
				uni.navigateTo({
					url: "/pages/appointment/appointment"
				})
			},

			gotoFuzeren() {
				uni.navigateTo({
					url: "/pages/leader_change/leader_change"
				})
			},




			async loadData() {
				let carouselList = await this.$api.json('carouselList');
				this.swiperLength = carouselList.length;
				this.carouselList = carouselList;
				let goodsList = await this.$api.json('goodsList');
				this.goodsList = goodsList || [];
			},

			// swiperChange(e) {
			// 	const index = e.detail.current;
			// 	this.swiperCurrent = index;
			// 	this.titleNViewBackground = this.carouselList[index].background;
			// },
			//详情页
			navToDetailPage(item) {
				//测试数据没有写id，用title代替
				let id = item.title;
				uni.navigateTo({
					url: `/pages/product/product?id=${id}`
				})
			},
		},
		// #ifndef MP
		// 标题栏input搜索框点击
		onNavigationBarSearchInputClicked: async function(e) {
			this.$api.msg('点击了搜索框');
		},
		//点击导航栏 buttons 时触发
		onNavigationBarButtonTap(e) {
			const index = e.index;
			if (index === 0) {
				this.$api.msg('点击了扫描');
			} else if (index === 1) {
				// #ifdef APP-PLUS
				const pages = getCurrentPages();
				const page = pages[pages.length - 1];
				const currentWebview = page.$getAppWebview();
				currentWebview.hideTitleNViewButtonRedDot({
					index
				});
				// #endif
				uni.navigateTo({
					url: '/pages/notice/notice'
				})
			}
		}
		// #endif
	}
</script>

